@import "../app/app-variables";
@import "../app/app";

ul.categoriess {
    height: 100%;
    max-height: 100%;

    display: flex;
    flex-flow: row wrap;
    // This defines the alignment along the main axis.
    justify-content: space-around;
    // This defines the default behavior for how flex items are laid out along the cross axis on the current line.
    align-items: stretch;
    // This aligns a flex container's lines within when there is extra space in the cross-axis
    align-content: space-around;

    li.categories:nth-child(odd) {
        background-color: $bg-cd;
        color: $cl;
    }

    li.categories:nth-child(even) {
        background-color: $bg-cl;
        color: $cd;
    }

    li.categories {
        // flex-shrink: 3;
        flex-grow: 2;
        flex-basis: 30%;
        max-width: 50%;
        min-height: 50%;
        height: 50%;

        //
        cursor: pointer;
        display: block;
        max-height: 50%;
        overflow: hidden;
        position: relative;
    }

    div.category-icon {
        height: $gr-h * 100%;

        svg.category-icon {
            transform: scale($gr-h) translateY($gr-h / 2 * 100%);
        }
    }

    h2.category-name {
        height: $gr-l * 100%;
        padding: 0 $gr-lll * 100%;
    }
}
