@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";

div.ing-prefs {
    height: 100%;
    position: relative;

    span.ing-prefs {
        align-items: center;
        display: flex;
        height: $gr-lh * $grs-lh * 100%;
        justify-content: center;
        margin: 0 auto;
        // outline: 1px solid gray;
        position: relative;
        top: $gr-h * 100%;
        transform: translateY(-50%);
        width: $gr-hhl * 100%;
    }

    span.ing-prefs-wanted {
        background-color: #0ba9b3;
        // color: $cd;
        font-weight: bold;
    }

    span.ing-prefs-wanted-not {
        background: none;
        background-color: #80707d;
        color: #ff93ee;
        top: $gr-h * 100%;
        // transform: translate($gr-llh * 100%, $gr-lll * 100%) rotate($f-13 * 1deg);
        transform: translateX($gr-lll * 100%) rotate($f-8 * 1deg);
        text-decoration: line-through;
    }

    button.ing-prefs-want-not {
        // background: firebrick;
        background: transparent;
        border: 1px solid #80707d;
        border-radius: $gr-ll * 100%;
        bottom: 0;
        height: $gr-lh * 100%;
        left: $gr-lll / 2 * 100%;
        overflow: hidden;
        position: absolute;
        text-align: center;
        width: $gr-ll * 100%;

        img.ing-prefs-want-not {
            display: inline-block;
            height: 100%;
            width: auto;
        }
    }

    img.ing-prefs-want {
        background: transparent;
        height: $gr-hh * 100%;
        // outline: 1px solid gray;
        position: absolute;
        top: $gr-ll / 2 * 100%;
        transform: translateX(-50%);
    }
}

div.ing-prefs-inactive {
    background: #cccccc;
    color: #ffffff;
    text-decoration: line-through;
}
