@import "../../app/app-variables";
@import "../../app/app";
@import "../categories";

div.category-icon {
    display: block;
    height: 100%;
    position: relative;
    text-align: center;

    // background-color: darkviolet;

    svg.category-icon {
        height: 100%;
        max-width: 100%;
        width: auto;
    }

    image.category-icon {
        height: $gr-hl * 100%;
        width: $gr-hl * 100%;
        transform: translate(($gr-lh/2) * 100%, ($gr-lh/2) * 100%);
    }

    image.cat-random {
        height: $gr-h * 100%;
        width: $gr-h * 100%;
        transform: translate(($gr-l/2) * 100%, ($gr-l/2) * 100%);
    }

    // categories

    circle.cat-alco {
        fill: $cat-alco-bc;
    }

    circle.cat-custom {
        fill: $cat-custom-bc;
    }

    circle.cat-list {
        fill: $cat-list-bc;
    }

    circle.cat-prefs {
        fill: $cat-prefs-bc;
    }

    circle.cat-random {
        fill: $cat-random-bc;
    }

    circle.cat-top {
        fill: $cat-top-bc;
    }
}
