@import "../../../app/app-variables";
@import "../../../app/app";
@import "../../_categories";

aside.cat-aside-narrow {
    background-image: linear-gradient(to right, rgba(1, 0, 0, 0), rgba(0, 0, 0, 1));
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    width: $gr-llh * 100%;

    div.cat-aside-narrow {
        font-size: $gr-h * 100%;
        height: $gr-hh * 100%;
        opacity: $gr-l;
        position: relative;
    }

    nav.cat-aside-narrow {
        height: $gr-ll * 100%;
        opacity: $gr-h;
    }
}
