@import "../../app/app-variables";
@import "../..//app/app";

div.drink {
  // background-color: wheat;
  display: block;
  height: 100%;
  position: relative;

  h2.drink-name {
    // background-color: mediumorchid;
    height: $gr-lh * 100%;
    position: relative;
  }

  div.drink-icon {
    // background-color: yellowgreen;
    display: block;
    float: left;
    height: $gr-hl * 100%;
    width: $gr-lh * 100%;
  }

  ul.drink-ings {
    // background-color: powderblue;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    float: left;
    height: $gr-hl * 100%;
    list-style-type: disc;
    position: relative;
    width: $gr-h * 100%;
  }

  li.drink-ings {
    margin-left: $gr-llh * 100%;
    padding: $gr-lll * 1em 0;
    position: relative;
  }

  nav.drink {
    // background-color: mediumaquamarine;
    display: block;
    float: left;
    height: $gr-hl * 100%;
    text-align: center;
    width: $gr-ll * 100%;
  }

  button.drink {
    padding: $gr-lll * 100%;
    top: $gr-h * 100%;
    transform: translateY(-50%);
  }
}
