@import "../../app/app-variables";
@import "../../app/app";
@import "../categories";

h2.category-name {
    display: flex;
    justify-content: center; /* align horizontal */
    align-items: center; /* align vertical */

    height: 100%;
    position: relative;
    text-align: center;
}

h2.cat-alco {
    // color: $cat-alco-c;

    background: linear-gradient($cat-alco-c1, $cat-alco-c2);
    background-clip: text;
    color: transparent;
}

