@import "../../app/app-variables";
@import "../../app/app";

div.drinks-list-item {
  // background-color: tan;
  display: block;
  height: 100%;
  position: relative;
  text-align: center;

  h2.drink-name {
    // background-color: yellow;
    height: $gr-h * 100%;
    position: relative;
  }

  ul.drink-ings {
    // background-color: skyblue;
    list-style-type: none;
    height: $gr-l * 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 auto;
    width: $gr-hl * 100%;
  }

  li.drink-ings {
    align-self: center;
    flex: 1 1 auto;
    height: 38%;
  }

  span.drink-ings {
    display: block;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }
}

li.drinks-list:nth-child(odd) {
  div.drinks-list-item {
    li.drink-ings:not(:last-child) {
      border-right: ($gr-llh * 1em) solid $bg-cl;
    }
  }
}

li.drinks-list:nth-child(even) {
  div.drinks-list-item {
    li.drink-ings:not(:last-child) {
      border-right: ($gr-llh * 1em) solid $bg-cd;
    }
  }
}
