@import "../app/app-variables";
@import "../app/app";

ul.ings {
    height: 100%;
    overflow: hidden;
    position: relative;

    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: stretch;

    li.ings {
        // background-image: url("/ings/want.svg");
        // background-repeat: no-repeat;
        // background-position: center;
        // background-size: $gr-h * 100% $gr-h * 100%;

        // outline: 3px solid red;
        cursor: pointer;
        display: block;
        overflow: hidden;
        position: relative;
        text-align: center;
        width: 33.33%;
    }

    li.ings:nth-child(odd) {
        background-color: $bg-cd;
        color: $cl;
    }

    li.ings:nth-child(even) {
        background-color: $bg-cl;
        color: $cd;

        span.ings {
            color: $cd;
        }
    }

}
