@import "../../../app/app-variables";
@import "../../../app/app";
@import "../../_categories";

div.cat-aside-narrow,
div.cat-aside-wide {
    div.category-icon {
        height: $gr-lh * 100%;
    }

    svg.category-icon {
        transform: scale($gr-hh);
    }

    h2.category-name {
        height: $gr-hl * 100%;
        position: relative;
        text-align: center;
        text-orientation: upright;
        width: 100%;
        writing-mode: vertical-lr;
    }
}
