  // button, button animation

  @keyframes sheen {
    50% {
      transform: translate(-100%, 100%);
    }

    60% {
      transform: rotateZ(60deg) translate(1em, -9em);
    }

    100% {
      transform: translate(100%, -100%);
    }
  }

  button {
    // background-color: $btn-bg;
    border-radius: $grs-h * 1em;
    // color: $btn-c;
    cursor: pointer;
    font-size: $grs-l * 2vh;
    // padding: $grs-lll * 1em;
    position: relative;
    overflow: hidden;
  }

  button::after {
    content: "";
    position: absolute;
    top: -50%;
    right: -50%;
    bottom: -50%;
    left: -50%;
    background: linear-gradient(
      to bottom,
      rgba(229, 172, 142, 0),
      rgba(255, 255, 255, 0.5) 50%,
      rgba(229, 172, 142, 0)
    );
    transform: rotateZ(60deg) translate(-5em, 7.5em);

    animation-name: sheen;
    animation-duration: 6s;
    animation-timing-function: linear;
    // animation-delay: 3s;
    animation-iteration-count: infinite;
    animation-direction: forwards;
  }

  //

  .cursor {
    cursor: pointer;
  }

  //

  .textc {
    text-align: center;
  }

  //

  .center {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .center-h {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    transform: translateX(-50%);
  }

  .center-v {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  .center-top-grh {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-h * 100%;
    transform: translate(-50%, -50%);
  }

  .center-top-grl {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-l * 100%;
    transform: translate(-50%, -50%);
  }

  .center-top-grlh {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-lh * 100%;
    transform: translate(-50%, -50%);
  }

  .center-top-grll {
    left: 50%;
    margin-right: -50%;
    position: absolute;
    top: $gr-ll * 100%;
    transform: translate(-50%, -50%);
  }
